

































import Vue from 'vue'
import VTableRow from '@/components/UI/table/TableRow'
import TableCell from '@/components/UI/table/TableCell'
import { HeaderItemType, RowItem, CellItem } from './types'

export default Vue.extend({
  components: {
    VTableRow,
    TableCell
  },
  props: {
    head: {
      type: [Array as () => HeaderItemType[], Object as () => HeaderItemType],
      default: null
    },
    items: {
      type: Array as () => RowItem[],
      default: () => []
    },
    editable: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      sortBy: '',
      sortDirection: 1
    }
  },
  computed: {
    headerItems (): HeaderItemType[] {
      if (typeof this.head === 'object') {
        return Object.keys(this.head).map((value): HeaderItemType => {
          return {
            value,
            text: (this.head as HeaderItemType)[value],
            sortable: true
          }
        })
      }

      return this.head
    },
    currentItems (): RowItem[] {
      if (!this.sortBy) {
        return this.items
      }

      return [...this.items].sort((firstItem: CellItem[], secondItem: CellItem[]) => {
        const firstVal: CellItem | undefined = firstItem.find(cell => cell.value === this.sortBy)
        const secondVal: CellItem | undefined = secondItem.find(cell => cell.value === this.sortBy)

        if (!firstVal || !secondVal) {
          return 1
        }

        const result = firstVal.text > secondVal.text ? 1 : -1

        return result * this.sortDirection
      })
    }
  },
  methods: {
    handleHeadCellClick (headItem: HeaderItemType): void {
      if (!headItem.sortable) return

      if (this.sortBy === headItem.value) {
        this.sortDirection = -1 * this.sortDirection
        return
      }

      this.sortDirection = 1
      this.sortBy = headItem.value
    },
    handleCellUpdate (item: HeaderItemType) {
      this.$emit('cellUpdate', item)
    }
  }
})
