






























import Vue from 'vue'

type Position = 'left' | 'center' | 'right'
enum CellType {
  TH = 'th',
  TD = 'td'
}

export default Vue.extend({
  props: {
    headItem: {
      type: Boolean,
      default: false
    },
    editable: {
      type: Boolean,
      default: false
    },
    position: {
      type: String as () => Position,
      default: 'left'
    },
    text: {
      type: [String, Number],
      required: true
    },
    value: {
      type: [String, Number],
      required: true
    },
    clickable: {
      type: Boolean,
      default: false
    },
    rowIndex: {
      type: [String, Number],
      default: ''
    }
  },
  computed: {
    tagName (): string {
      return this.headItem ? CellType.TH : CellType.TD
    },
    styles () {
      return {
        'table-cell--header': this.headItem,
        [`table-cell--${this.position}`]: true
      }
    }
  },
  methods: {
    handleInputChange (event: Event): void {
      const element = event.currentTarget as HTMLInputElement
      const { value } = element

      this.$emit('cellUpdate', {
        value: this.value,
        text: value,
        rowIndex: this.rowIndex
      })
    }
  }
})
